.product-form__container {
  max-width: 1600px;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.product-form__title {
  font-family: Inria Sans;
  font-size: 32px;
  font-weight: 700;
  line-height: 38.37px;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  margin-bottom: 20px;
}

.product-form__label {
  font-size: 14px;
  display: block;
  margin-top: 15px;
  margin-bottom: 5px;
}

.product-form__input,
.product-form__select,
.product-form__textarea {
  width: 1600px;
  height: 58px;
  padding: 10px;
  border-radius: 28px     ;
  border: 1px solid #ccc;
  font-size: 14px;
}

.product-form__textarea {
  height: 80px;
}

.product-form__input--price {
  width: calc(100% - 20px);
  display: inline-block;
}

.product-form__image-upload {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 150px;
  border: 2px dashed #e0e0e0;
  border-radius: 5px;
  margin-top: 20px;
  color: #e53935;
  cursor: pointer;
}

.product-form__image-upload-icon {
  width: 50px;
  height: 50px;
}

.product-form__image-upload-text {
  margin-left: 10px;
}

.product-form__save-button {
  display: block;
  width: 100%;
  padding: 12px;
  font-size: 16px;
  color: #fff;
  background-color: #e53935;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}